.container {
  height: 100vh;
  height: -webkit-fill-available;
  max-width: 500px;
  margin: auto;
  flex-direction: column;
  display: flex;
  background: #f7f7f7;
}

.button {
  height: 100%;
  width: 100%;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.3);
  font-size: 8vh;
  user-select: none;

  &:nth-child(1) {
    background: #ef476f;
  }
  &:nth-child(2) {
    background: #ffd166;
  }
  &:nth-child(3) {
    background: #06d6a0;
  }
  &:nth-child(4) {
    background: #118ab2;
  }
  &:nth-child(5) {
    background: #073b4c;
  }
  &:nth-child(6) {
    background: #7678ed;
  }

  &:hover {
    opacity: 0.9;
  }
  &:active {
    opacity: 0.7;
    text-shadow: 2px 2px rgba(0, 0, 0, 0.3);
  }
}
